

/* eslint-disable no-unused-vars */


import React, { Component } from "react"
import MetaTags from 'react-meta-tags';
import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  CardImg,
  CardText,
  CardSubtitle,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
} from "reactstrap"

import {
  CButton,
  CCard,
  CCardHeader,
  CCardBody,
  CCardFooter,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import Select from "react-select"
import axios from 'axios'
import  { Redirect } from 'react-router-dom'

import { French } from "flatpickr/dist/l10n/fr.js";
import { format } from 'date-fns';

// import Flatpickr from "react-flatpickr"
import Flatpickr from "flatpickr";

import DatePicker from "react-datepicker";
import { registerLocale, setDefaultLocale } from  "react-datepicker";
import es from 'date-fns/locale/es';
import $ from 'jquery';

import ReactSelect from 'react-select'
import makeAnimated from 'react-select/animated';
import ville from "../../dict/ville.js";
import { storage } from "../../../firebase/firebase";
import conf from '../../../conf.js';
const animatedComponents = makeAnimated();


// const cities = require('all-the-cities');

registerLocale('es', es)


  const optionGroup = [
    {
      options: [
        { label: "PDF", value: "PDF" },
        { label: "MP4", value: "MP4" },
        { label: "JPEG", value: "JPEG" },
        { label: "MP3", value: "MP3" },
        { label: "EXCEL", value: "EXCEL" },
      ]
    },
  ]
  const optionTypeEntreprise = [
    {
      options: [
        { label: "SRL", value: "srl" },
        { label: "SA", value: "sa" },
        { label: "PME", value: "pme" }
      ]
    },
  ]
  const optionPaye = [
    {
      options: [
        { label: "Payé", value: true },
        { label: "Impayé", value: false },
      ]
    },
  ]
  const optionArchive = [
    {
      options: [
        { label: "Archivé", value: true },
        { label: "Pas archivé", value: false },
      ]
    },
  ]

class DynamicFormAccueil extends Component {
    constructor(props) {
        super(props)
        this.state = {
          textApropos:"",
          titreApropos:"",
          idApropos:0,
          idPub:0,
          telephone:"",
          email:"",
          adresse:"",
          service:[],
          fichier:[],
          video:"",
          file:[],
          home:[],
          pub:[]
        }
        this.handleUpdateHome.bind(this)
        this.handleRemoveHome.bind(this)
        this.handleRemovePub.bind(this)
        this.handleUpdateFile.bind(this)
        this.handleRemoveFile.bind(this)
        this.handleUpdateService.bind(this)
        this.handleRemoveService.bind(this)
        this.handleImage.bind(this)
        this.handleUpdatePub.bind(this)
    }

    componentDidMount() {
      
      $("#propos").css("display","block")

      let  token=  JSON.parse(localStorage.getItem('token_auth'))
   
      const requestOptions = {
        method: 'GET',
        headers: { 
        'Authorization':'Bearer '+token.token,
      }
    };
  
    fetch(conf.url+"acceuil/propos/propos",requestOptions)
    .then((response) => response.json())
    .then((data) => {
       console.log("propos====>",data)
       try{
       // $("#propos").css("display","none")
        this.setState({titreApropos:data[0].titre})
        this.setState({textApropos:data[0].text})
        this.setState({adresse:data[0].adresse})
        this.setState({telephone:data[0].telephone})
        this.setState({email:data[0].email})
        this.setState({video:data[0].urlVideo})
        this.setState({idApropos:data[0].id})
       }catch(error){

       }
       
     }
     
    );

    fetch(conf.url+"acceuil/homeone/homeones",requestOptions)
    .then((response) => response.json())
    .then((data) => {
      console.log("home====>",data)
       this.setState({home:data})
       
        this.handleImage(data)
      
      
          
      
     }
     
    );

    fetch(conf.url+"acceuil/pub/pubs",requestOptions)
    .then((response) => response.json())
    .then((data) => {
      console.log("home====>",data)
       this.setState({pub:data})
       
       data.map((el,idx)=>{
        if(el.multimedia.file!=undefined){
        storage.ref("images")
        .child(el.multimedia.file).getDownloadURL()
        .then((url) => {
          
            data[idx].multimedia.file=url
            this.setState({pub:data})
        
        })
        }
       })
      
      
          
      
     }
     
    );

    
    fetch(conf.url+"acceuil/fichier/fichiers",requestOptions)
    .then((response) => response.json())
    .then((data) => {
      console.log("fichier====>",data)
 
      
       data.map((el,idx)=>{
         try{
            if(el.multimedia){
            storage.ref("images")
            .child(el.multimedia.file).getDownloadURL()
            .then((url) => {
              
                data[idx].multimedia.file=url
                this.setState({fichier:data})
            
            })
            }
      }catch(error){
        this.setState({fichier:data})
      }
       })
      
      
          
      
     }
     
    );


    fetch(conf.url+"acceuil/service/services",requestOptions)
    .then((response) => response.json())
    .then((data) => {
       this.setState({service:data})
       console.log("service",data)

       data.map((el,idx)=>{
        if(el.multimedia.file!=undefined){
        storage.ref("images")
        .child(el.multimedia.file).getDownloadURL()
        .then((url) => {
          
            data[idx].multimedia.file=url
            this.setState({service:data})
        
        })
        }
       })
      
     }
     
    );
  
  
    }

    handleImage=(data)=>{

      data.map((el,idx)=>{
        if(el.multimedia.file!=undefined){
        storage.ref("images")
        .child(el.multimedia.file).getDownloadURL()
        .then((url) => {
            data[idx].multimedia.file=url
            this.setState({home:data})
        
        })
        }
       })

    }


 


    handleUpdatePub=(id)=>{
     
        localStorage.setItem('idPub',id);
        this.setState({ok: <Redirect to='/modifier-publicite'/>});
      
      
    }


    handleUpdatePropos=()=>{
      if(this.state.idApropos!=0){
        localStorage.setItem('idApropos',this.state.idApropos);
        this.setState({ok: <Redirect to='/modifier-apropos'/>});
      }
      
    }

    handleRemoveHome=(id)=>{
         
    
      let  token=  JSON.parse(localStorage.getItem('token_auth'))
   
      const requestOptions = {
        method: 'GET',
        headers: { 
        'Authorization':'Bearer '+token.token,
      }
    };
  
  
    fetch(conf.url+"homeone/homeones/delete/"+id,requestOptions)
    .then((response) => response.json())
    .then((data) => {
      console.log("remove",data)
      this.setState({home:this.state.home.filter((val)=>val.id!==id)})
      
     }
     
    );
      
    }

    
    handleRemovePub=(id)=>{
         
    
      let  token=  JSON.parse(localStorage.getItem('token_auth'))
   
      const requestOptions = {
        method: 'GET',
        headers: { 
        'Authorization':'Bearer '+token.token,
      }
    };
  
  
    fetch(conf.url+"pub/pubs/delete/"+id,requestOptions)
    .then((response) => response.json())
    .then((data) => {
      console.log("remove pub",data)
      this.setState({pub:this.state.pub.filter((val)=>val.id!==id)})
      
     }
     
    );
      
    }

    handleRemoveService=(id)=>{
         
    
      let  token=  JSON.parse(localStorage.getItem('token_auth'))
   
      const requestOptions = {
        method: 'GET',
        headers: { 
        'Authorization':'Bearer '+token.token,
      }
    };
  
  
    fetch(conf.url+"service/services/delete/"+id,requestOptions)
    .then((response) => response.json())
    .then((data) => {
      console.log("remove",data)
      this.setState({service:this.state.service.filter((val)=>val.id!==id)})
      
     }
     
    );
      
    }
    handleRemoveFile=(id)=>{
         
    
      let  token=  JSON.parse(localStorage.getItem('token_auth'))
   
      const requestOptions = {
        method: 'GET',
        headers: { 
        'Authorization':'Bearer '+token.token,
      }
    };
  
  
    fetch(conf.url+"fichier/fichiers/delete/"+id,requestOptions)
    .then((response) => response.json())
    .then((data) => {
      console.log("remove",data)
      this.setState({fichier:this.state.fichier.filter((val)=>val.id!==id)})
      
     }
     
    );
      
    }

    handleUpdateHome=(id)=>{
      
        localStorage.setItem('idHome',id);
        this.setState({ok: <Redirect to='/modifier-homeone'/>});
      
      
    }
    handleUpdateFile=(id)=>{
      
      localStorage.setItem('idFichier',id);
      this.setState({ok: <Redirect to='/modifier-fichier'/>});
    
    
     }
    handleUpdateService=(id)=>{
      
      localStorage.setItem('idService',id);
      this.setState({ok: <Redirect to='/modifier-service'/>});
    
    
  }

  render() {

    const { pick } = this.state
    const { selectedGroup } = this.state
    const { selectedLangue } = this.state
    const animatedComponents = makeAnimated();
    
  return (
        <>
          <React.Fragment>
            <div className="page-content">
             {this.state.ok}

            <Container fluid={false} className="justify-content-center">

              <Row className="justify-content-center">
              <Col md="12" lg="12" xl="12">

                {/* <Row ClassName="mb-3">
                  <Col md="8">
                      <div className="page-title-box d-flex align-items-center justify-content-between">
                        <h4 className="mb-0 font-size-18">Nouveau compte</h4>
                      </div>
                  </Col>
                </Row> */}
     
                <h1 className="p-4 text-center" >Paramètres    <i class='bx bxs-wrench'></i></h1>

                <Card ClassName="mt-5">
                  {/* <CCardHeader className="p-4"> */}
                    
                  {/* </CCardHeader> */}

                  <CardBody>
                    
                    <hr/>
                    
                    <Row className="mb-3"> 
                      
                      <Col md="8">
                        <h3>Home One</h3>
                      </Col>

                      <Col md="4" className="pull-right">
                        <Button
                          color="success"
                          className="font-16 btn btn-success float-right"
                          href="/#/ajout-homeone"
                        >
                          <i class='bx bx-plus-circle' > </i>  Ajouter un HomeOne
                        </Button>
                      </Col>
                    </Row>
                    
                    
                    <Row className="mb-2"> 
                    {
                      this.state.home.map((element,idx) =>(
                        <Col md="6" lg="6"> 
                          <Card>
                            {
                              (element.multimedia.file!=undefined)?(
                                <CardImg top width="100%" src={element.multimedia.file} alt="Image carousel" />
                              ):(
                                <CardImg top width="100%" src="" alt="Image carousel" />
                              )
                            }
                            
                            <CardBody>
                              <CardTitle tag="h5">{element.titre}</CardTitle>
                              <CardSubtitle tag="h6" className="mb-2 text-muted">Texte du Bouton: {element.bouton}</CardSubtitle>
                              <CardText>{element.description}</CardText>

                              <Row> 
                                <Col> 

                                </Col>

                                <Col style={{textAlign:"right"}}> 
                                <a> 
                                  <i style={{cursor:"pointer"}} onClick={this.handleUpdateHome.bind(this,element.id)} className="bx bx-pencil bx-sm text-primary mr-2"/> 
                                  <i style={{cursor:"pointer"}} onClick={this.handleRemoveHome.bind(this,element.id)} className="bx bx-trash  bx-sm text-danger "/>
                                </a>
                                 
                                </Col>
                              </Row>

                            </CardBody>
                          </Card>
                        </Col> 
                      ))
                    }
                    </Row> 

                </CardBody>
                </Card>

                
                <Card>
                <CardBody>
                    <hr/>

                    <Row className="mb-3 mt-3"> 
                      <Col md="8">
                        <h3>A propos</h3>
                      </Col>

                      <Col md="4" className="pull-right">
                        <Button
                          color="success"
                          className="font-16 btn btn-success float-right"
                          id="propos"
                          href="/#/ajout-apropos"
                        >
                          <i class='bx bx-plus-circle' > </i>  Ajouter à propos
                        </Button>
                      </Col>
                    </Row>
                   
                        <Col> 
                          <Card>
                            <CardBody>
                              <CardTitle tag="h5">  {this.state.titreApropos}  </CardTitle>
                              <CardSubtitle tag="h6" className="mb-2 text-muted">Adresse:   {this.state.adresse}</CardSubtitle>
                              <CardSubtitle tag="h6" className="mb-2 text-muted">Telephone:   {this.state.telephone}</CardSubtitle>
                              <CardSubtitle tag="h6" className="mb-2 text-muted">Email:   {this.state.email}</CardSubtitle>
                              <CardText>{this.state.textApropos}</CardText>
                              
                              <Row className="mt-3">
                                <label>Vidéo</label>
                                <div className="embed-responsive embed-responsive-16by9 mb-4">
                                  <iframe className="embed-responsive-item" src={this.state.video} height="100" width="200" allowfullscreen frameborder="4"></iframe>
                                </div>
                              </Row>

                              <Row> 
                                <Col md="3"> 
                                  <a> 
                                    <i style={{cursor:"pointer"}} onClick={this.handleUpdatePropos} className="bx bx-pencil bx-sm text-primary "/>
                                  </a>
                               
                                </Col>

                               
                              </Row>

                            </CardBody>
                          </Card>
                        </Col> 
                    
                </CardBody>
                </Card>
                    
                
                <Card>
                <CardBody>

                    <hr/>


                    <Row className="mb-3 mt-3"> 
                      <Col md="8">
                        <h3 >Service</h3>
                      </Col>

                      <Col md="4" className="pull-right">
                        <Button
                          color="success"
                          className="font-16 btn btn-success float-right"
                          href="/#/ajout-service"
                        >
                          <i class='bx bx-plus-circle' > </i>  Ajouter un service
                        </Button>
                      </Col>
                    </Row>
                    
                    <Row> 
                    
                    {
                      this.state.service.map((element,idx) =>(
                        
                        <Col md="6" lg="6"> 
                          <Card>
                            {/* <CardImg top width="100%" src="/assets/256x186.svg" alt="Image service" /> */}
                            {
                              (element.multimedia.file!=undefined)?(
                                <CardImg top width="100%" src={element.multimedia.file} alt="Image service" />
                              ):(
                                <CardImg top width="100%" src="" alt="Image service" />
                              )
                            }
                            <CardBody>
                              <CardTitle tag="h5">{element.titre}</CardTitle>
                              <CardText>{element.description}</CardText>

                              <Row> 
                                <Col> 
                                  
                                </Col>

                                <Col style={{textAlign:"right"}}> 
                                <a> 
                                  <i style={{cursor:"pointer"}} onClick={this.handleUpdateService.bind(this,element.id)} className="bx bx-pencil bx-sm text-primary mr-2"/> 
                                  <i style={{cursor:"pointer"}} onClick={this.handleRemoveService.bind(this,element.id)} className="bx bx-trash  bx-sm text-danger "/> 
                                </a>
                                 
                                </Col>
                              </Row>

                            </CardBody>
                          </Card>
                        </Col> 
                      ))
                    }
                  </Row> 

                </CardBody>
                </Card>

                
                <Card>
                <CardBody>

                    <hr/>

                    <Row className="mb-3 mt-3"> 
                      <Col md="8">
                        <h3 >Fichier</h3>
                      </Col>

                      <Col md="4" className="pull-right">
                        <Button
                          color="success"
                          className="font-16 btn btn-success float-right"
                          href="/#/ajout-fichier"
                        >
                          <i class='bx bx-plus-circle' > </i>  Ajouter un fichier
                        </Button>
                      </Col>
                    </Row>
                    
                    
                    
                    {
                      this.state.fichier.map((element,idx) =>(
                        <Col md="6" lg="6"> 
                        <Card>
                          {
                            element.multimedia?
                            <CardImg top width="100%" src={element.multimedia.file} alt="Image fichier" />:
                            <CardImg top width="100%" src="#" alt="Image fichier" />

                          }
                          
                          <CardBody>
                            <CardTitle tag="h5">{element.typeFichier}</CardTitle>
                            <CardText>{element.description}</CardText>

                            <Row> 
                              <Col> 
                                 
                              </Col>

                              <Col style={{textAlign:"right"}}> 
                                <a> <i style={{cursor:"pointer"}} onClick={this.handleUpdateFile.bind(this,element.id)} className="bx bx-pencil bx-sm text-primary mr-2"/> 
                                    <i style={{cursor:"pointer"}} onClick={this.handleRemoveFile.bind(this,element.id)} className="bx bx-trash  bx-sm text-danger "/> </a>
                                 
                                </Col>
                            </Row>

                          </CardBody>
                        </Card>
                        </Col>
                      ))
                    }
                </CardBody>
                </Card>

                
                <Card>
                <CardBody>
                    <hr/>


                    {/* Publicité */}
                    <Row className="mb-3 mt-3"> 
                      <Col md="8">
                        <h3 >Publicité</h3>
                      </Col>

                      <Col md="4" className="pull-right">
                        <Button
                          color="success"
                          className="font-16 btn btn-success float-right"
                          href="/#/ajout-publicite"
                        >
                          <i class='bx bx-plus-circle' > </i>  Ajouter une publicité
                        </Button>
                      </Col>
                    </Row>

                    <Row>
                    {
                      this.state.pub.map((element,idx) =>(
                    <Col md="6" lg="6"> 
                    <Card>
                    {
                              (element.multimedia.file!=undefined)?(
                                <CardImg top width="100%" src={element.multimedia.file} alt="Image carousel" />
                              ):(
                                <CardImg top width="100%" src="" alt="Image carousel" />
                              )
                            }
                      <CardBody>
                        <Row>
                          <Col>
                            <CardTitle tag="h5">Date de début:</CardTitle>{format(new Date(element.dateDebut),'dd/MM/yyyy')}
                          </Col>
                          <Col>
                            <CardTitle tag="h5">Date de fin:</CardTitle>{format(new Date(element.dateFin),'dd/MM/yyyy')}
                          </Col>
                        </Row>
                        <Row>
                        <Col>
                          <CardTitle tag="h5" style={{marginTop:"10px"}}>Affichage</CardTitle>{element.endroit}
                        </Col>
                        </Row>
                        <Row>
                        <Col> <a href={element.urlPub}>{element.urlPub}</a></Col>
                        </Row>

                        <Row> 
                          <Col></Col>
                          <Col style={{textAlign:"right"}}> 
                            <a> 
                              <i onClick={this.handleUpdatePub.bind(this,element.id)} style={{cursor:"pointer"}}  className="bx bx-pencil bx-sm text-primary mr-2"/> 
                              <i style={{cursor:"pointer"}} onClick={this.handleRemovePub.bind(this,element.id)}  className="bx bx-trash  bx-sm text-danger "/> 
                            </a>
                          </Col>
                        </Row>
                      </CardBody>
                    </Card>
                    </Col>
                      ))
                      }
                    </Row>

                  </CardBody>
                </Card>
              </Col>
            </Row>
          </Container>
        </div>
             
      </React.Fragment>
    </>
        )}

  }
  export default DynamicFormAccueil